@import 'theme';

ul.gallery {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  max-width: $max-width;
  margin: auto;

  & > li {
    display: block;
    height: $thumbnail-height + $gap / 2;
    width: $thumbnail-width + $gap / 2;
    padding: $gap / 2;

    @media(max-width: 565px) {
      width: $thumbnail-width / 2 + $gap / 2;
      height: $thumbnail-height / 2 + $gap / 2; }

    & img {
      height: $thumbnail-height + $gap / 2;
      width: $thumbnail-width + $gap / 2;
      @media(max-width: 565px) {
        width: $thumbnail-width / 2 + $gap / 2;
        height: $thumbnail-height / 2 + $gap / 2; } } } }




